import * as React from 'react';
import MD from 'react-markdown';
import { Box, Heading, Icon } from '@chakra-ui/core';

export default function Card({ card, icon, index }) {
  return (
    <Box p={4} flexBasis="320px" flexGrow="1">
      <Box mb={2} display="grid" gridTemplateColumns="1fr" alignItems="center" justifyItems="center">
        {/* <Icon name={`blob${index}`} size="7em" color={`${card.color}.300`} gridRow="1" gridColumn="1" /> */}
        <Box backgroundColor="green.400" transform="skewX(5deg) rotateX(20deg)" />
        {icon && <Icon name={icon} size="6em" gridRow="1" gridColumn="1" color="gray.700" />}
      </Box>
      <Heading size="xs" textTransform="uppercase" letterSpacing="0.03em" mb={1} color={`${card.color}.600`}>
        {card.title}
      </Heading>
      <MD source={card.content} />
    </Box>
  );
}
